/**
 *
 * Tab Scroll
 *
 * @author Takuto Yanagida
 * @version 2021-11-11
 *
 */


window['NACSS'] = window['NACSS'] || {};


(function (NS) {

	/**
	 *
	 * Scroll
	 *
	 * @author Takuto Yanagida
	 * @version 2021-10-23
	 *
	 */
	
	
	function initialize(cs, opts = {}) {
		if (cs.length === 0) return;
	
		opts = Object.assign({
			styleBar    : ':ncTabBar',
			styleCurrent: ':ncCurrent',
			styleActive : ':ncActive',
			hashPrefix  : 'tsc:',
		}, opts);
		const is = [];
		for (let i = 0; i < cs.length; i += 1) {
			const inst = create(cs[i], i + 1, opts);
			if (inst) is.push(inst);
		}
		window.addEventListener('hashchange', () => is.forEach(onHashChanged));
	}
	
	function onHashChanged(inst) {
		const tar = getCurrentByHash(inst, location.hash);
		inst.active = tar;
		setTimeout(() => update(inst), 10);
	}
	
	
	// -------------------------------------------------------------------------
	
	
	function create(cont, cid, opts) {
		const inst = {
			opts,
			vs    : null,
			uls   : [],
			active: null,
		}
		const hs = extractHeaders(cont, cid, opts.hashPrefix);
		if (hs.length === 0) return false;
	
		const bars = [];
		for (const h of hs) {
			const bar = createBar(hs, h, opts);
			cont.insertBefore(bar.ul, h.elm);
			bars.push(bar);
			inst.uls.push(bar.ul);
		}
		assignEvent(inst, bars);
	
		inst.active = getCurrentByHash(inst, location.hash);
		setTimeout(() => update(inst), 10);
		return inst;
	}
	
	function getCurrentByHash(inst, hash) {
		const id = hash.replace('#', '');
		if (id !== '') {
			const tar = document.getElementById(id);
			if (tar && inst.uls.indexOf(tar) !== 0) {
				return tar;
			}
		}
		return null;
	}
	
	
	// -------------------------------------------------------------------------
	
	
	function extractHeaders(cont, cid, prefix) {
		const fh = getFirstHeading(cont);
		if (!fh) return [];
		const tn = fh.tagName;
		const hs = [];
	
		for (const elm of Array.from(cont.children)) {
			if (elm.tagName === tn) {
				const id = `${prefix}${cid}-${hs.length + 1}`;
				hs.push({ elm, id });
			}
		}
		return hs;
	}
	
	function createBar(hs, curH, opts) {
		const ul = document.createElement('ul');
		ul.id = curH.id;
		ul.className = '';  // for Dummy
		setClass(ul, opts.styleBar);
		const as = [];
	
		for (const h of hs) {
			const a = createAnchor(h);
			const li = document.createElement('li');
			if (h === curH) setClass(li, opts.styleCurrent);
			li.appendChild(a);
			ul.appendChild(li);
			as.push(a);
		}
		return { ul, as };
	}
	
	
	// -------------------------------------------------------------------------
	
	
	function assignEvent(inst, bars) {
		for (const bar of bars) {
			bar.as.forEach((a, i) => {
				a.addEventListener('click', () => onClick(inst, bars[i].ul));
			});
		}
		onIntersect((vs) => {
			inst.vs = vs;
			update(inst);
		}, inst.uls, 0);
	}
	
	function onClick(inst, clicked) {
		inst.active = clicked;
		setTimeout(() => {
			if (inst.active) {
				const y = inst.active.getBoundingClientRect().top;
				inst.active = null;
				if (window.innerHeight < y) update(inst);
			}
		}, 1000);
	}
	
	function update(inst) {
		const tar = inst.active ? inst.active : getFirstVisible(inst);
		for (const ul of inst.uls) {
			setClass(ul, inst.opts.styleActive, ul === tar);
		}
	}
	
	function getFirstVisible(inst) {
		if (!inst.vs) return null;
		return inst.uls.find((ul, i) => inst.vs[i]);
	}
	
	NS.tabScroll = initialize;

	/**
	 *
	 * Style Class Utilities
	 *
	 * @author Takuto Yanagida
	 * @version 2021-11-11
	 *
	 */
	
	
	function hasClass(tar, cls) {
		const key = cls.substr(1);
		if (cls.startsWith(':')) {
			return tar.dataset[key] !== undefined;
		} else {
			return tar.classList.contains(key);
		}
	}
	
	function setClass(tar, cls, enabled = true, val = '') {
		const key = cls.substr(1);
		if (cls.startsWith(':')) {
			if (enabled) {
				tar.dataset[key] = val;
			} else {
				delete tar.dataset[key];
			}
		} else {
			const c = [key, val].join('-');
			if (enabled) {
				tar.classList.add(c);
			} else {
				tar.classList.remove(c);
			}
		}
	}
	
	function getSelector(cls) {
		if (cls.startsWith(':')) {
			return `*[data-${cls.substr(1).replace(/([A-Z])/g, c => '-' + c.charAt(0).toLowerCase())}]`;
		} else {
			return `*${cls}`;
		}
	}
	
	/**
	 *
	 * Utilities
	 *
	 * @author Takuto Yanagida
	 * @version 2021-11-17
	 *
	 */
	
	
	function getScrollOffset() {
		const s = getComputedStyle(document.documentElement);
		return parseInt(s.scrollPaddingTop);
	}
	
	
	// -----------------------------------------------------------------------------
	
	
	function throttle(fn) {
		let isRunning;
		return (...args) => {
			if (isRunning) return;
			isRunning = true;
			requestAnimationFrame(() => {
				isRunning = false;
				fn(...args);
			});
		};
	}
	
	const resizeListeners = [];
	
	function onResize(fn, doFirst = false) {
		if (doFirst) fn();
		resizeListeners.push(throttle(fn));
	}
	
	document.addEventListener('DOMContentLoaded', () => {
		window.addEventListener('resize', () => {
			for (const l of resizeListeners) l();
		}, { passive: true });
	});
	
	
	// -----------------------------------------------------------------------------
	
	
	function onIntersect(fn, targets, threshold = 0, rootMargin = '* 0px 0px 0px') {
		const vs = Array(targets.length).fill(false);
		const tm = new Map();
		targets.forEach((t, i) => tm.set(t, i));
	
		function init() {
			const io = new IntersectionObserver((es) => {
				for (const e of es) vs[tm.get(e.target)] = e.isIntersecting;
				fn(vs);
			}, { rootMargin: rootMargin.replace('*', `${mt}px`), threshold });
			for (const t of targets) io.observe(t);
			return io;
		}
		let mt = 0;
		let io = null;
		let st = null;
		onResize(() => {
			mt = -getScrollOffset();
			if (st) clearTimeout(st);
			st = setTimeout(() => {
				if (io) io.disconnect();
				io = init();
			}, 100);
		}, true);
	}
	
	/**
	 *
	 * Common Functions
	 *
	 * @author Takuto Yanagida
	 * @version 2021-10-23
	 *
	 */
	
	
	function getFirstHeading(container) {
		const cs = container.children;
		for (let i = 0; i < cs.length; i += 1) {
			if (/^H[1-6]$/.test(cs[i].tagName)) return cs[i];
		}
		return null;
	}
	
	function createAnchor(h) {
		const a = document.createElement('a');
		a.href  = '#' + h.id;
		a.title = h.elm.innerText;
		const t = h.elm.title ? h.elm.title : h.elm.innerHTML;
	
		a.innerHTML = t;
		a.querySelectorAll('small').forEach(e => e.remove());
		if (a.innerText === '') {
			a.innerHTML = t;
		}
		spanning(a);
		return a;
	}
	
	function spanning(e) {
		for (const c of Array.from(e.children)) {
			if (c.tagName === 'A' || c.tagName === 'SMALL') {
				const s = document.createElement('span');
				s.innerHTML = c.innerHTML;
				e.replaceChild(s, c);
				spanning(s);
			} else {
				spanning(c);
			}
		}
	}
	

})(window['NACSS']);
